<template>
  <div>
    <!---->
    <div class="m-content">
      <!---->
      <el-form ref="ruleForm" :model="ruleForm" :rules="rules" class="demo-ruleForm" label-position="top"
               label-width="100px">
        <!--url-->
        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="" prop="title">
              <el-input v-model="ruleForm.title" placeholder=""></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="URL" prop="firstPicture">
              <el-input v-model="ruleForm.firstPicture" placeholder=""></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <!---->
        <el-form-item label="" prop="description">
          <mavon-editor @dblclick.native="autoWrite" v-model="ruleForm.description"/>
        </el-form-item>
        <el-form-item label="" prop="content">
          <mavon-editor  v-model="ruleForm.content"></mavon-editor>
        </el-form-item>
        <!---->
        <el-row :gutter="20">
          <el-col :span="20">
            <el-form-item label="" prop="cate">
              <el-select v-model="ruleForm.typeId" :allow-create="true" :filterable="true" placeholder="" style="width: 100%;">
                <el-option v-for="type in types" :key="type.id" :label="type.typeName" :value="type.id"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <!---->
          
        </el-row>
        <!---->
        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="" prop="words">
              <!-- -->
              <el-input v-model="ruleForm.words" placeholder="" type="number" @dblclick.native="computeWords"></el-input>
            </el-form-item>
          </el-col>
          <!---->
          <el-col :span="12">
            <el-form-item label="" prop="views">
              <el-input v-model="ruleForm.views" placeholder=" 0" type="number"></el-input>
            </el-form-item>
          </el-col>
          <!---->
          <el-col :span="12">
            <el-form-item label="" prop="publised">
              <el-tooltip :content="'Switch value: ' + ruleForm.status" placement="top">
                <el-switch v-model="ruleForm.status" :active-value="1" :inactive-value="0"></el-switch>
              </el-tooltip>
            </el-form-item>
          </el-col>
        </el-row>
        <!---->
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
          <el-button @click="resetForm('ruleForm')"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "BlogEdit",
  data() {
    return {
      types: {},
      ruleForm: {
        id: '',
        title: '',
        description: '',
        content: '',
        firstPicture: '',
        typeId: '',
        createTime: '2020-05-22 22:05:49',
        updateTime: '2020-05-22 22:05:49',
        userId: 1,
        words: null,
        views: 0,
        status: 0,
      },
      rules: {
        title: [
          {required: true, message: '', trigger: 'blur'},
          {min: 2, max: 45, message: ' 3  45 ', trigger: 'blur'}
        ],
        description: [
          {required: true, message: '', trigger: 'blur'}
        ],
        content: [
          {required: true, message: '', trigger: 'blur'}
        ],
        words: [{required: true, message: '', trigger: 'change'}],
      }
    }
  },
  methods: {
    //100
    autoWrite() {
      if(this.ruleForm.content.length<100){
        this.ruleForm.description = this.ruleForm.content.substring(0,this.ruleForm.content.length)
      }
      else{
        this.ruleForm.description = this.ruleForm.content.substring(0,100)
      }
    },
    //
    computeWords() {
      var contents = this.ruleForm.content
      this.ruleForm.words = contents.replace(/^[\s\uFEFF\xA0]+|[\s\uFEFF\xA0]+$/g, '').length
    },
    //
    submitForm(formName) {
      this.computeWords()
      this.$refs[formName].validate((valid) => {
        if (valid) {
          const _this = this
          if(_this.ruleForm.id==""){
            this.$axios.post('/blog/create', this.ruleForm).then(res => {
              console.log(res)
              _this.$alert('', '', {
                confirmButtonText: '',
                callback: action => {
                  _this.$router.push("/blogList")
                }
              });
            })
          }
          else{
            this.$axios.post('/blog/update', this.ruleForm).then(res => {
              console.log(res)
              _this.$alert('', '', {
                confirmButtonText: '',
                callback: action => {
                  _this.$router.push("/blogList")
                }
              });
            })
          }

        }
        else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    //
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    //
    getTypes() {
      const _this = this
      this.$axios.get('/types').then(res => {
        _this.types = res.data.data
      })
      console.log(this.types)
    },
    //id
    getBlogById() {
      const blogId = this.$route.params.blogId
      const _this = this
      if (blogId) {
        this.$axios.get('/blog/detail/' + blogId).then(res => {
          const blog = res.data.data
          _this.ruleForm.id = blog.id
          _this.ruleForm.title = blog.title
          _this.ruleForm.firstPicture = blog.firstPicture
          _this.ruleForm.description = blog.description
          _this.ruleForm.content = blog.content
          _this.ruleForm.words = blog.words
          _this.ruleForm.views = blog.views
          _this.ruleForm.typeId = blog.typeId
          _this.ruleForm.status = blog.status
          _this.ruleForm.userId = blog.userId
          _this.ruleForm.createTime = blog.createTime
          _this.ruleForm.updateTime = blog.updateTime
        })
      }
    }
  },
  created() {
    this.getTypes();
    this.getBlogById();
  }
}
</script>
<style scoped>
.m-content {
  width: 100%;
}
</style>