<template>
  <!--  -->
  <div class="form">
    <h3>
      
      <el-button v-show="this.realParentCommentId !== -1" class="m-small" size="mini" type="primary"
                 @click="toSendParentId()">
      </el-button>
    </h3>
    <el-form ref="formRef" :model="commentForm" :rules="rules" size="small">
      <el-input v-model="commentForm.content" :class="'textarea'" :rows="5" :validate-event="false" maxlength="250"
                placeholder="" show-word-limit type="textarea"></el-input>

      <el-row :gutter="20">
        <el-col :span="6">
          <el-form-item prop="nickname">
            <el-popover ref="nicknamePopover" content="QQ" placement="bottom" trigger="focus"></el-popover>
            <el-input v-model="commentForm.nickname" v-popover:nicknamePopover :validate-event="false" placeholder=""
                      @blur="onInputBlur">
              <i slot="prefix" class="el-input__icon el-icon-user"></i>
            </el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item prop="email">
            <el-popover ref="emailPopover" content="" placement="bottom" trigger="focus"></el-popover>
            <el-input v-model="commentForm.email" v-popover:emailPopover :validate-event="false" placeholder="">
              <i slot="prefix" class="el-input__icon el-icon-message"></i>
            </el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item prop="website">
            <el-popover ref="websitePopover" content="" placement="bottom" trigger="focus"></el-popover>
            <el-input v-model="commentForm.website" v-popover:websitePopover :validate-event="false"
                      placeholder="">
              <i slot="prefix" class="el-input__icon el-icon-map-location"></i>
            </el-input>
          </el-form-item>
        </el-col>

        <el-col :offset="1" :span="5">
          <el-form-item>
            <!--   -->
            <el-button size="medium" type="primary"  v-throttle="[postForm,`click`,30000]"  @mouseenter.native="beforePost"></el-button>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>


  </div>
</template>

<script>

export default {
  name: "CommentForm",
  props: {
    realParentCommentId: {
      type: Number,
      default: '-1'
    },
    realParentCommentNickname: {
      type: String,
      default: ""
    }

  },
  data() {
    var checkEmail = (rule, value, callback) => {
      // 
      const regEmail = /^\w+@\w+(\.\w+)+$/
      if (regEmail.test(value)) {
        return callback()
      }
      //
      callback(new Error(''))
    }

    return {

      commentForm: {
        content: "",
        nickname: "",
        email: "",
        website: "",
        avatar: "",
        blogId: 0,
        parentCommentId: this.realParentCommentId,
        isAdminComment: -1,
        qq: "",
        parentCommentNickname: this.realParentCommentNickname
      },
      rules: {
        nickname: [
          {required: true, message: ''},
          {max: 15, message: '15', trigger: 'blur'}
        ],
        email: [
          {required: true, message: ''},
          {min: 2, max: 30, message: '302', trigger: 'blur'},
          {validator: checkEmail}
        ],
        website: [

          {max: 30, message: '30'}
        ],

      }

    }
  },
  methods: {
    //
    //1>hashCode
    //2>hashCode
    hashFunc(str, size) {
      //1.hashCode
      var hashCode = 0
      //2. hashCode
      for (var i = 0; i < str.length; i++) {
        hashCode = 37 * hashCode + str.charCodeAt(i) //
      }
      //3.
      var index = hashCode % size + 1
      return index
    },
    //  id
    toSendParentId() {
      this.$emit('parentEvent', '')
      //console.log("")
      this.commentForm.parentCommentId = -1
    },
    // qq qq
    onInputBlur() {
      //qq
      const _this = this
      var qq = this.commentForm.nickname
      if (!isNaN(Number(qq)) && qq.length > 4 && qq.length < 12) {
        this.$axios.get('https://api.usuuu.com/qq/' + qq).then(res => {
          if (res.data) {
            _this.commentForm.nickname = res.data.data.name
            _this.commentForm.avatar = res.data.data.avatar
            _this.commentForm.qq = res.data.data.qq
            //console.log(JSON.stringify(_this.commentForm))
            //console.log("")
          }
        })
      } else {
        //
        var randomNum = this.hashFunc(this.commentForm.nickname, 20)
        this.commentForm.avatar = "https://cdn.jsdelivr.net/gh/yubifeng/blog-resource/bloghosting/2021/avatar/avatar" + randomNum + ".webp"
        //console.log(JSON.stringify(this.commentForm))
      }
    },
    //
    beforePost() {
      if (this.commentForm.avatar == "") {
        this.onInputBlur()
      }
      //console.log("sdaf ")

    },

    //
    postForm() {
      //console.log(JSON.stringify(this.commentForm))
      //
      this.$refs.formRef.validate((valid) => {
        if (valid) {
          console.log(JSON.stringify(this.commentForm))
          //()
          if (this.commentForm.isAdminComment == 1) {
            this.commentForm.avatar = "https://cdn.jsdelivr.net/gh/yubifeng/blog-resource/bloghosting//website/static/websiteAvatar.webp"
          }
          const _this = this
          this.$axios.post('/comment/add', this.commentForm).then(res => {
            //console.log(res)
            if (res.data.code == 200) {
              _this.$alert('', '', {
                confirmButtonText: '',
                callback: action => {
                  //_this.$router.push("/blog/")
                  location.reload()
                }
              });
            } else {
            }
          })
          // //alert('submit!');
          // //
          // this.$refs.formRef.resetFields();
          // this.commentForm.content = ""
          // this.commentForm.qq = ""
          // this.commentForm.avatar = ""
        } else {
          console.log('error submit!!');
          alert('');
          return false;
        }
      });
    }
  },
  watch: {
    //props  
    realParentCommentId: function (newVal) {
      this.commentForm.parentCommentId = newVal
      console.log(JSON.stringify(this.commentForm))
    }
  },
  created() {
    if (this.$route.params.blogId) {
      this.commentForm.blogId = this.$route.params.blogId
    } else if (this.$route.path == "/about") {
      this.commentForm.blogId = 1
    } else if (this.$route.path == "/friends") {
      this.commentForm.blogId = 11
    } else {
      alert("error")
      return false
    }
    if (this.$store.getters.getUser) {
      if(this.$store.getters.getUser.role==="role_admin"||this.$store.getters.getUser.role==="role_root") {
        this.commentForm.isAdminComment = 1;
      }
    }
  },
}
</script>

<style scoped>
.form h3 {
  margin: 5px;
  font-weight: 500 !important;
}
.form .m-small {
  margin-left: 5px;
  padding: 4px 5px;
}
.el-form .textarea {
  margin-top: 5px;
  margin-bottom: 15px;
}
.el-form textarea {
  padding: 6px 8px;
}
.el-form textarea, .el-form input {
  color: black;
}
.el-form .el-form-item__label {
  padding-right: 3px;
}
</style>