<template>
  <div v-if="ifShow">

    <!---->
    <el-form inline>
      <el-form-item>
        <el-button type="primary" size="small" icon="el-icon-plus" @click="addDialogVisible=true"></el-button>
      </el-form-item>
    </el-form>

    <el-table :data="userList">
      <el-table-column label="" type="index" width="50"></el-table-column>
      <el-table-column label="" width="80">
        <template v-slot="scope">
          <el-avatar shape="square" :size="60" fit="contain" :src="scope.row.avatar"></el-avatar>
        </template>
      </el-table-column>
      <el-table-column label="" prop="username"></el-table-column>
      <el-table-column label="" prop="nickname"></el-table-column>
      <el-table-column label="" prop="email"></el-table-column>
      <el-table-column label="" prop="createTime"></el-table-column>
      <el-table-column label="" prop="updateTime"></el-table-column>
      <el-table-column label="" width="100">
        <template v-slot="scope">
          <el-switch v-model="scope.row.status" :active-value="1" :inactive-value="0"  @change="userPublishedChanged(scope.row.id)"></el-switch>
        </template>
      </el-table-column>
      <el-table-column label="" prop="role" width="100"></el-table-column>



      <el-table-column label="" width="200">
        <template v-slot="scope">
          <el-button icon="el-icon-edit" size="mini" type="primary" @click="showEditDialog(scope.row)"></el-button>
          <el-popconfirm icon="el-icon-delete" iconColor="red" title="" @confirm="deleteUserById(scope.row.id)">
            <el-button slot="reference" icon="el-icon-delete" size="mini" type="danger"></el-button>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.currentPage"
                   :page-sizes="[10, 20, 30, 50]" :page-size="queryInfo.pageSize" :total="total"
                   layout="total, sizes, prev, pager, next, jumper" background>
    </el-pagination>



    <!---->
    <el-dialog title="" width="40%" :visible.sync="addDialogVisible" :close-on-click-modal="false" @close="addDialogClosed">
      <!---->
      <el-form :model="addForm" :rules="formRules" ref="addFormRef" label-width="80px">

        <el-form-item label="" prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="nickname">
          <el-input v-model="addForm.nickname"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="addForm.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="addForm.email"></el-input>
        </el-form-item>
        <el-form-item label="URL" prop="avatar">
          <el-input v-model="addForm.avatar"></el-input>
        </el-form-item>
        <el-form-item label="" prop="role">
          <el-input v-model="addForm.role"></el-input>
        </el-form-item>
        <el-form-item label="" prop="published">
          <el-switch v-model="addForm.status" :active-value="1" :inactive-value="0" ></el-switch>
        </el-form-item>
      </el-form>
      <!---->
      <span slot="footer">
				<el-button @click="addDialogVisible=false"> </el-button>
				<el-button type="primary" @click="saveUser"> </el-button>
			</span>
    </el-dialog>

    <!---->
    <el-dialog title="" width="40%" :visible.sync="editDialogVisible" :close-on-click-modal="false" @close="editDialogClosed">
      <!---->
      <el-form :model="editForm" :rules="formRules" ref="editFormRef" label-width="80px">
        <el-form-item label="" prop="username">
          <el-input v-model="editForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="nickname">
          <el-input v-model="editForm.nickname"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="editForm.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="editForm.email"></el-input>
        </el-form-item>
        <el-form-item label="URL" prop="avatar">
          <el-input v-model="editForm.avatar"></el-input>
        </el-form-item>
        <el-form-item label="" prop="role">
          <el-input v-model="editForm.role"></el-input>
        </el-form-item>
        <el-form-item label="" prop="published">
          <el-switch v-model="editForm.status" :active-value="1" :inactive-value="0"></el-switch>
        </el-form-item>
      </el-form>
      <!---->
      <span slot="footer">
				<el-button @click="editDialogVisible=false"> </el-button>
				<el-button type="primary" @click="editUser"> </el-button>
			</span>
    </el-dialog>
  </div>

</template>

<script>
export default {
  name: "UserManager",
  data() {
    return {
      infoForm: {
        content: '',
        commentEnabled: true,
      },
      queryInfo: {
        currentPage: 1,
        pageSize: 10
      },
      ifShow:false,
      userList: [],
      total: 0,
      addDialogVisible: false,
      editDialogVisible: false,
      addForm: {
        nickname: '',
        description: '',
        website: '',
        avatar: '',
        isPublished: true
      },
      editForm: {
        nickname: '',
        description: '',
        website: '',
        avatar: '',
        isPublished: true
      },
      formRules: {
        nickname: [{required: true, message: '', trigger: 'blur'}],
        description: [{required: true, message: '', trigger: 'blur'}],
        website: [{required: true, message: '', trigger: 'blur'}],
        avatar: [{required: true, message: 'URL', trigger: 'blur'}],
      }
    }
  },
  methods:{
    //
    getUserList() {
      const _this = this
      this.$axios.get('/user/list?currentPage=' + this.queryInfo.currentPage+"&pageSize=" + this.queryInfo.pageSize).then((res) => {
        _this.userList = res.data.data.records
        _this.total = res.data.data.total
      });
    },

    handleCurrentChange(newPage) {
      this.queryInfo.currentPage = newPage
      this.getUserList()
    },
    handleSizeChange(newPageSize){
      this.queryInfo.pageSize = newPageSize
      this.getUserList()

    },
    showEditDialog(row){
      this.editForm = row
      this.editDialogVisible = true
    },
    editDialogClosed() {
      this.editForm = {}
      this.editDialogVisible = false
    },

    deleteUserById(id) {
      const _this = this
      this.$axios.get('/user/delete/' + id).then((res) => {
        _this.$alert('', '', {
          confirmButtonText: '',
          callback: action => {
            _this.getUserList()
            //_this.$router.push("/blogList")
          }
        })
      })
    },


    userPublishedChanged(id){
      const _this = this
      this.$axios.get('/user/publish/'+id).then((res) => {
        _this.$alert('', '', {
          confirmButtonText: '',
          callback: action => {
            _this.getUserList()
            //_this.$router.push("/blogList")
          }
        })
      })

    },
    saveUser(){
      const _this = this
      this.$axios.post('/user/create',this.addForm).then((res) => {
        _this.$alert('', '', {
          confirmButtonText: '',
          callback: action => {
            _this.addDialogVisible = false

            _this.getUserList()
            //_this.$router.push("/blogList")
          }
        })
      });
      this.$refs['addFormRef'].resetFields();
    },
    editUser(){
      const _this = this
      this.$axios.post('/user/update', this.editForm).then(res => {
        _this.$alert('', '', {
          confirmButtonText: '',
          callback: action => {
            _this.editDialogVisible = false
            _this.getUserList()
          }
        });
      });

    },
    commentEnabledChanged(){

    },
    addDialogClosed(){
      this.addDialogVisible = false
    },

  },
  created() {
    if(this.$store.getters.getUser.username){
      if(this.$store.getters.getUser.role=="role_root"){
        this.ifShow = true
        this.getUserList()
        // this.$message.error("")
      }
      else {
        this.$message.error("")
      }
    }
    else {
      this.$message.error("")
    }




  }
}
</script>